import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import { setActiveEvent } from '../app/redux/actions';
import EventInfo from '../event-info/event-info';
import Modal from '../modal/modal';

import Events from './__events/timetable__events';
import TimeSlots from './__time-slots/timetable__time-slots';

import './timetable.css';

class Timetable extends PureComponent {
    constructor(props) {
        super(props);

        this.closeModal = this.closeModal.bind(this);
    }

    componentWillUnmount() {
        this.closeModal();
    }

    closeModal() {
        const {setActiveEvent} = this.props;

        setActiveEvent(null);
    }

    render() {
        const {activeEvent} = this.props;

        return (
            <div className="timetable">
                <div className="timetable__content">
                    <TimeSlots />
                    <Events />
                </div>
                <Modal
                    isOpen={!!activeEvent}
                    onRequestClose={this.closeModal}>
                        <EventInfo mix="timetable__event-info" {...activeEvent} />
                </Modal>
            </div>
        )
    }
}

Timetable.propTypes = {
    match: PropTypes.object.isRequired
};

const mapStateToProps = ({app: {activeEvent}}) => ({activeEvent});

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({setActiveEvent}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(Timetable);



